import React from 'react';
import '../css/App.css';
function ResultsView(props){
	return(
		<div>
			<div onClick={()=>{window.open(props.song.url)}}>
				
				<img src={props.song.img!==""?props.song.img:"https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcREzffv9B8PWVOftHQkN3Q4C5aDAwcB4l4P_xJRv4eNilhwPwgq"} alt="cover"/>	
			</div>
			<div>
				<h3>{props.song.name}</h3>
				<h4>{props.song.artist}</h4>
								<br/>
				<p>{props.song.listeners} listeners</p>
			</div>	
		</div>	
		)
	}

export default ResultsView;