import React from 'react';
import axios from 'axios';
import {store} from '../stores/index';
let left = 20;
const API_KEY = "AIzaSyBJAkRT_rADL34jq7a_Hoh1XEyc9FLV5dE";
const CHANNEL_ID = "UC2pmfLm7iq6Ov1UwYrWYkZA";
class About extends React.Component{
	constructor(){
		super();
		this.state={
			videos:[]
		}
		this.nextVideo = this.nextVideo.bind(this);
	}
	componentDidMount(){
		axios
		.get(`https://www.googleapis.com/youtube/v3/videos?part=contentDetails&chart=mostPopular&videoCategoryId=10&maxResults=10&regionCode=${store.getState().countryCode}&key=${API_KEY}`)
		.then(reponse=>{
			const songs = reponse.data.items;
			for(let i = 0;i<songs.length;++i){
				this.setState({
					...this.state,
					videos:this.state.videos.concat({id:"https://www.youtube.com/embed/"+songs[i].id})
				});
			}
		});
		
	}	
	nextVideo(){
		let videos = document.getElementById('video-section');
		if(left>=-570){
			left-=70;
		}else{
			left=10;
		}
		videos.style.left=`${left}%`;
	}

	prevVideo(){
		let videos = document.getElementById('video-section');
		if(left<10){
			left+=70;
		}else{
			left=-570;
		}
		videos.style.left=`${left}%`;
	}
	render(){
		const videos = this.state.videos.map(v=>(
				<div>
					<iframe width="100%" height="100%" src={v.id} frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
				</div>
				))
	return(
		<div id="about-section">
			<div id="home-main-container" style={{'top':'-90px'}}>
			<h1>Top Trending
					<span style={{'box-shadow': '10px 10px 5px 0px rgba(0,0,0,0.75)',
					'color':'#df2029','padding':'10px','backgroundColor':'white'}}>
					Music Videos</span> in <span style={{'textDecoration':'overline',
					'color':'#df2029','backgroundColor':'white','padding':'10px',
					'box-shadow': '10px 10px 5px 0px rgba(0,0,0,0.75)'}}>{localStorage.getItem('country')}
					</span></h1>
			</div>
			<div id="video-section">
				{videos}
			</div>
			<h1 id="next-video-button"
			onClick={this.nextVideo}
			><i class="fas fa-chevron-right"></i></h1>

			<h1 id="prev-video-button"
			onClick={this.prevVideo}
			><i class="fas fa-chevron-left"></i></h1>
			</div>
		
		);
	}
}
export default About;